body{
    margin: 0;
    padding: 0;
    background-color: #191923;
}
#page-logo{
    width: 65px;
    height: 65px;
    border-radius: 50%;
    background-color: #D01B46;
}

#logo-anchor{
    display: flex;
    justify-content: center;
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
    margin: 60px auto;
}

::-webkit-scrollbar {
    width: 10px;
}

::-webkit-scrollbar-track {
    box-shadow: inset 0 0 1px grey; 
    border-radius: 10px;
}

::-webkit-scrollbar-thumb {
    background: rgb(110, 110, 110); 
    border-radius: 10px;
}

::-webkit-scrollbar-thumb:hover {
    background: rgb(90, 90, 90); 
}
#SearchBar{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

#search-cell{
    display: flex;
    align-items: center;
}

.search-input{
    opacity: 0.67;
    width: 40vw;
    height: 7vh;
    border: 1px rgb(102, 102, 102) ;
    border-radius: 50px;
    outline: none;
    padding-left: 40px;
    font-size: 20px;
    color: rgb(126, 126, 126);
}

#search-cell img{
    width: 4vw;
    cursor: pointer;
}

#search-cell img:nth-child(0n + 2){
    margin: 0 20px;
}
#config-container *{
    font-family: 'Montserrat';
}
.config-container{
    margin: 30px auto;
    -webkit-transition: 0.2s;
    transition: 0.2s;
    width: auto;
    height: 0;
    background-color: #323241;
    overflow: hidden;
}
#posts-container #option-list{
    -webkit-transition: 1s;
    transition: 1s;
}

.item-cell{
    display: flex;
    align-items: center;
    margin: 0 24px;
}

.active{
    height: 55px;
}

span, label{
    font-size: 15px;
    color: #E6E6E6;
}

li, ul{
    display: flex;
    list-style-type: none;
    padding: 0;
}

li input[type=radio], input[type=checkbox]{
    opacity: 0;
}
li input[type=text]{
    margin-left: 15px;
    border-radius: 50px;
    padding: 2px 10px;
    width: 50px;
    outline: none;
    border: 1px rgb(102, 102, 102) ;
    background-color: #3B3B46;
    color: #e6e6e6;
    margin-right: 10px;
}
li label{
    margin: 0 -5px;
    cursor: pointer;
    padding: 2px 10px;
    border-radius: 50px;
    background-color: #3B3B46;
}



.check-with-sortby:checked + .sortby-for-check,
.check-with-orderby:checked + .orderby-for-check,
.check-with-hide:checked + .hide-for-check {
background-color: #24242E;
}
.details-container{
    -webkit-transition: 0.13s ease;
    transition: 0.13s ease;
    height: 100vh;
    width: 0px;
    position: fixed;
    bottom: 0;
    left: 0;
    background-color: #323241ed;
    overflow-y: scroll;
    display: flex;
    align-items: center;
    z-index: 1000;
}
.active{
    width: 60%;
}

.closeBtn{
    position: absolute;
    top: 0;
    right: 0;
    width: 20px;
    margin: 25px;
    padding: 7px;
    cursor: pointer;
}
#details{
    flex-direction: column;
    display: flex;
    padding: 25px;
    width: 90%;
    margin: auto;
}
.ref-links{
    position: absolute;
    right: 0;
    margin: 7px 60px 0 0;
}

.ref-links > a{
    font-family: 'Montserrat';
    color: #D4D4D4;
    font-size: 12px;
    text-decoration: none;
    padding: 5px 13px; 
    margin-right: 10px;
    border: 1px solid #56566E;
    border-radius: 15px;  
}

.sub-name{
    color: #F3F3F3;
    width: -webkit-max-content;
    width: -moz-max-content;
    width: max-content;
}

.sub-name > h1{
    font-size: 21px;
    font-weight: 700;
    margin: 5px 0;
}

.author-name{
    color: #E6E6E6;
    font-size: 17px;
    font-weight: 500;
    margin: 7px 0;
}

.author-name > a{
    color: #E6E6E6;
}

.title{
    color: #DEDEDE;
    font-size: 15px;
    margin: 10px 0;
}
.stats{
    color: #E6E6E6;
    font-family: 'Montserrat';
    font-size: 15px;
}

.embedded-cell{
    margin: 0 auto;
}

.embedded-cell > img, .embedded-cell > video, .embedded-cell > iframe{
    max-width: 100%;
}
.twitch-embed, .gfycat-embed, .pornhub-embed, .youporn-embed{
    height: 500px;
}
.youtube-embed{
    max-height: -webkit-fill-available;
}
#posts-container{
    width: 80vw;
    margin: 0 auto;
}
.error-message{
    font-size: 20px;
    font-weight: 700;
    color: #C1C1C5;
    text-align: center;
    margin-top: 5px;
}
.post-cell{
    margin: 3px;
    width: 24%;
    overflow: hidden;
    cursor: pointer;
}
.flag{
    position: absolute;
    z-index: 100;
    padding: 3px 7px;
    margin: 5px;
    border-radius: 50px;
    color: #efefef;
    font-weight: 600;
    font-size: 65%;
    opacity: 1;
    -webkit-transition: 0.125s ease;
    transition: 0.125s ease;
}

.nsfw{
    right: 0;
}

.image > img{
    width: 100%;
}
.blur{
    -webkit-filter: blur(40px);
            filter: blur(40px)
}
.post-overlay{
    -webkit-transition: 0.2s ease;
    transition: 0.2s ease;
    height: 100%;
    width: 100%;
    position: absolute;
    top: 0;
    opacity: 1;
    cursor: pointer;
    -webkit-transform: translateY(250%);
            transform: translateY(250%);
}
.post-cell:hover > .post-overlay{
    -webkit-transform: translateY(0);
            transform: translateY(0);
}
.post-cell:hover > .image > .flag{
    opacity: 0;
}
.post-details{
    padding: 10px;
    position: absolute;
    bottom: 0;
    margin-bottom: 5px;
}

.post-details h1{
    font-size: 100%;
    font-weight: 700;
    color: #FEFEFE;
    margin: 5px 0;
}
.post-details span{
    font-size: 14px;
    color: #dadada;
}
.post-details h2{
    font-size: 95%;
    font-weight: 500;
    color: #FEFEFE;
    margin: 7px 0 0 5px;
}

.arrow{
    width: 3vw;
    position: fixed;
    position: -webkit-sticky; 
    top: 44.5%;
    cursor: pointer;
    margin: 0 15px;
    opacity: 0.75;
}

.next-btn{
    right: 0;
}

.previous-btn{
    left: 0;
    -webkit-transform: scaleX(-1);
            transform: scaleX(-1)
}  

.invisible{
    display: none;
}
